main{
    padding-bottom: 70px;
}

h3{
    font-size: 30px;
    padding-top: 40px;
}

.main-content{
    background-color: rgba(0, 0, 0, 0.5);
    max-width: 70%;
    margin: 0 auto !important;
    box-shadow: 0 4px 8px 0 rgba(245, 245, 245, 0.2);
    transition: 0.3s;
    color: whitesmoke;
}

.main-content:hover{
    box-shadow: 0 8px 16px 0 rgba(245, 245, 245, 0.2);
}

.instructions-list{
    list-style-type: none;
    display: block;
    text-align: left;
    padding: 15px 0 0px 15px;
}

.instruction-number{
    background-color:rgba(83, 79, 79, 0.1);
    color: rgba(0, 255, 255, 0.6);
    width: 80px;
    height: 80px;
    /* padding-bottom: 5px; */
    margin-bottom: 5px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
}

.instruction-number span{
    font-size: 60px;
}

.instruction{
    display: flex;
    flex-direction: row;
    padding-bottom: 15px;
}

.instruction p{
    margin-left: 20px;
    font-size: 16px;
    display: flex;
    align-items: center;
}

#x, #o{
    font-size: 25px;
    padding: 2px;
}

#x{
    color: orange;
}

#o{
    color: red;
}

.video-option{
    display: flex;
    flex-basis: 100%;
    align-items: center;
    color: whitesmoke;
    margin: 8px 0;
}

span.video-prompt{
    color: rgba(0, 255, 255, 0.6);
    font-weight: bold;
}

.video-option:before, 
.video-option::after{
    content: "";
    flex-grow: 1;
    background: whitesmoke;
    height: 1px;
    font-size: 0px;
    line-height: 0px;
    margin: 0px 8px;
}

.video-player{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 30px 0 40px 0;
}

.video-frame{
    border-radius: 5px;
}

/* mobile styles for the page */
@media only screen and (max-width:480px){

    h3{
        font-size: 20px;
    }

    .main-content{
        max-width: 100%;
    }

    .instructions-list{
        display: block;
        padding: 30px 0 0 50px;
    }

    .instruction-number{
        background-color: rgba(83, 79, 79, 0.3);
        margin-bottom: 10px;
        width: 40px;
        height: 40px;
        /* margin-bottom: 10px; */
        border-radius: 50% !important;
        display: flex;
        justify-content: center;
    }

    .instruction-number span{
        font-size: 25px;
        display: flex;
        align-items: center;
    }

    .instruction{
        display: flex;
        flex-direction: row;
        padding-bottom: 30px;
    }

    .instruction p{
        margin-left: 10px;
        font-size: 15px;
        display: flex;
        align-items:flex-start;
        width: moz-fit-content;
        width: fit-content;
        margin-top: -.1em;
    }

    #x, #o{
        font-size: 20px;
        /* padding: 1px; */
    }

    .video-frame{
        width: 95%;
        height: 180px;
    }
    

}